@keyframes videoFadeIn {
	0% {
		transform: scale(0.5) translateY(-150vh);
	}

	50% {
		transform: scale(0.5) translateY(0vh);
	}

	100% {
		transform: scale(1) translateY(0vh);
	}
}

@keyframes videoFadeOut {
	50% {
		transform: scale(0.5);
	}

	100% {
		transform: scale(0.5) translateY(-150vh);
		visibility: hidden;
	}
}

@keyframes Proj1FadeInFromBot {
	50% {
		transform: translateY(0vh);
	}

	100% {
		transform: translateY(-75vh);
	}
}

@keyframes Proj1FadeInFromTop {
	0% {
		transform: translateY(-150vh);
	}

	100% {
		transform: translateY(-75vh);
	}
}

@keyframes Proj1FadeOutToBot {
	0% {
		transform: translateY(-75vh);
	}

	50% {
		transform: translateY(0vh);
	}
}

@keyframes Proj1FadeOutToTop {
	0% {
		transform: translateY(-75vh);
	}

	100% {
		transform: translateY(-150vh);
	}
}

